/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 22px;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 53px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 15px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 15px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 0 0 7px;
		    text-transform: uppercase;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle();

		    float: left;
		    line-height: 14px;
		    font-size: 22px;
	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 15px 0 3px;
		    float: right;
		    width: inherit;

		    .star-rating {
		    	font-size: 15px;
		    	letter-spacing: 6px;
		    }

		    .woocommerce-review-link {
		    	display: none;
		    }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 30px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 43px 0;
		    padding: 20px 0;
		    border-top: 1px solid $default-border-color;
		    border-bottom: 1px solid $default-border-color;
		
		    > span {
			    display: block;
			    margin: 10px 0;
			    font-size: 18px;
			    text-transform: uppercase;

			    span:first-child,
				a:first-child {
			    	margin-left: 6px;
			    	font-size: 15px;
			    }

				&.tagged_as {
					>a {
						font-size: 15px;
					}
				}
			
			    a, span {
    		    	text-transform: initial;
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }

		    .qodef-social-network-icon {
		    	font-size: 13px;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-text-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
		    margin: 36px 0 0;
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 40px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 30px 0 0;

		.wc-tabs {
			border-bottom: 1px solid $default-border-color;
		}
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: center;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;

				&:after {
					display: inline-block;
					content: "\002b";
					font-size: 22px;
					margin: 12px 17px 12px 0;
					text-align: center;
					width: 15px;
					top: 2px;
					position: relative;
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 15px;
					text-transform: uppercase;
					font-size: 18px;
				}

				&.active:after {
					content: "\2013";
					top: -1px;
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();
			padding: 11px 0 23px;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 18px 0 20px;
				text-align: center;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-text-color;
				padding: 0 10px 0 0;
				display: block;
				margin: 10px 0;
				font-weight: 300;
				text-transform: uppercase;
				font-size: 18px;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 22px;
				line-height: 24px;
				margin: 8px 0 34px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					border-bottom: 1px solid $default-border-color;

					&.comment-author-admin {

						.woocommerce-review__author {
							text-transform: uppercase;
						}
					}
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 22px 100px;

					.woocommerce-review__author {
						font-family: Raleway, sans-serif;
						margin-right: 5px;
						font-size: 18px;
						letter-spacing: .05em;
					    font-weight: 300;
						display: block;
						text-transform: uppercase;
					}

					.woocommerce-review__dash {
						display: none;
					}
					
					p {
						margin: 0;
						padding-bottom: 7px;
					}

					.star-rating {
						float: none;
						margin-bottom: 7px;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
					text-transform: uppercase;
					font-size: 22px;
					padding: 20px 0 10px 0;
					display: block;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include qodefWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2{
			font-size: 25px;
			display: block;
			margin: 36px 0 35px;
			padding-bottom: 15px;
			text-align: center;
			text-transform: uppercase;
			border-bottom: 1px solid $default-border-color;
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */