/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;

        margin: 0 0 60px;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {

        .qodef-widget-title {
            text-transform: uppercase;
            margin-bottom: 30px;
            margin-top: 0;
        }
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include qodefRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include qodefRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .qodef-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .qodef-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #e5e5e5;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 10px;
                    width: 3px;
                    background-color: $additional-dark-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include qodefTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $additional-dark-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 33px 0 0;
            
            .button {
                float: right;
            }
            
            .price_label {
                float: left;
            }
        }
    }
    
    &.widget_product_categories {
        .qodef-widget-title-holder {
            .qodef-widget-title{
                margin-bottom: 19px;
                margin-top: 0;
            }
        }
        
        ul {
            
            li {
                margin: 2px 0;
                line-height: 25px;
                
                ul {
                    margin: 6px 0 0 10px;
                }
            }
        }

        > ul > li {
           margin-bottom: 12px;
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .qodef-widget-title {
            margin: 0 0 30px;
            text-transform: uppercase;
        }
        
        ul {
            
            li {
                min-height: 90px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 80px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                        text-transform: uppercase;
                        font-size: 18px;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    display: none;
                    position: relative;
                    margin: 7px 0 0 0;
                    
                    span {
                        max-width: 100px;
                    }
                }

                del {
                    margin: 0 5px 0 103px;
                }

                ins {
                    margin: 0;
                }
                
                del,
                ins {
                    display: inline-block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                    margin-top: 15px;
                    margin-bottom: 10px;
                    letter-spacing: .05em;
                }
                
                .amount {
                    color: $default-text-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {

        .qodef-widget-title-holder .qodef-widget-title {
            text-transform: uppercase;
            margin-bottom: 23px;
            margin-top: 0;
        }
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 15px !important;
                margin-right: 10px;
                
                &:after {
                    content: '/';
                    color: $default-text-color;
                    margin: 0 0 0 1px;
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include qodefRelativeHolderLayout();
            clear: both;
            
            > div {
                @include qodefRelativeHolderLayout();
                box-sizing: border-box;
                border-bottom: 1px solid #d4d4d4;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include qodefTransition(color .2s ease-out);
            }
            
            input {
                width: calc(100% - 40px);
                font-size: 18px;
                font-family: 'Raleway', sans-serif;
                text-transform: uppercase;
    
                @include qodefPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 10px 4px;
                font-size: 20px;
                cursor: pointer;
                
                &:hover {
                    span {
                        color: $first-main-color;
                    }
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    font-size: 20px;
                    color: $default-text-color;
                    @include qodefTransition(color .2s ease-in-out);

                    &:hover {
                        color: $first-main-color;
                    }
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */