/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .qodef-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .qodef-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        .qodef-cart-number {
            font-size: 16px;
            top: -10px;
            font-weight: 400;
            position: relative;
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-cart-icon {
                font-size: 24px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .qodef-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $default-text-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.qodef-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 341px;
    background-color: #fff;
    border: 1px solid $default-border-color;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include qodefTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include qodefTableLayout();
            padding: 5px 0;
            box-sizing: border-box;

            &.qodef-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .qodef-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .qodef-item-info-holder {
        @include qodefTableCellLayout();
        vertical-align: middle;
        padding-left: 20px;

        .qodef-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            font-size: 18px;
            text-transform: uppercase;
        }

        .remove {
            position: absolute;
            top: 14px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .qodef-quantity {
            display: none;
            margin: 3px 0 0;
        }

        .amount {
            display: block;
            margin: 0;
            color: $default-text-color;
        }
    }

    .qodef-cart-bottom {
        margin-top: 30px;

        .qodef-subtotal-holder {
            position: relative;
            display: table;
            width: 200px;
            border-top: 1px solid #d4d4d4;
            padding-top: 13px;
            padding-bottom: 16px;
            margin: 0 auto;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-text-color;
                line-height: 20px;
                text-transform: uppercase;
                font-size: 18px;
                font-family: Raleway,sans-serif;
            }

            .qodef-total-amount {
                text-align: right;
	            font-size: 15px;
                font-family: "Open Sans",sans-serif;
            }
        }

        .qodef-btn-holder {
            text-align: center;
            margin-top: 25px;
        }

        .qodef-view-cart {
            @include qodefButtonDefaultStyle();
            @include qodefButtonMediumParams();
            @include qodefButtonSolidColor();
            white-space: nowrap;
            cursor: pointer;
            z-index: 3;
            width: 80%;
            padding: 15px 25px;

            &:hover {
                @include qodefButtonSolidHoverColor();
                background-color: #fff;
                border: 1px solid $default-text-color;
                color: $default-text-color;
            }

            span {
                display: block;
            }
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */