/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    .qodef-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: none;
            text-align: center;
            box-sizing: border-box;
            font-size: 20px;
        }

        .qodef-quantity-input {
            position: relative;
            height: 51px;
            width: 50px;
            padding: 0;
            margin: 0 22px 0 0;
            font-family: inherit;
            line-height: 42px;
            outline: 0;
            border-right: 1px solid $default-border-color;
            font-size: 18px;
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            position: absolute;
            right: 0;
            width: 22px;
            height: 22px;
            line-height: 20px;
            border-left: 0;
            cursor: pointer;
            @include qodefTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .qodef-quantity-minus {
            bottom: 0;
            padding-top: 2px;
            border-top: 1px solid $default-border-color;
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */