/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.qodef-content .woocommerce {
    @include qodefRelativeHolderLayout();
}

.qodef-pl-main-holder {
    margin: 0 0 67px;
    
    .qodef-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .qodef-pl-inner .qodef-pl-text {
                opacity: 1;
            }
        }
        
        .qodef-product-list-title {
            margin: 10px 0 7px;
            display: inline-block;
            line-height: 15px;
            text-transform: uppercase;
            letter-spacing: .05em;
        }
        
        .qodef-pl-rating-holder {
            @include qodefRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }
        
        .price {
            @include qodefWooCommercePriceStyle();
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .qodef-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }
        
        .qodef-pl-inner {
            @include qodefRelativeHolderLayout();
            overflow: hidden;
            
            .qodef-pl-image {
                @include qodefRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .qodef-pl-text {
                @include qodefAbsoluteHolderLayout();
                padding: 10px;
                opacity: 0;
                box-sizing: border-box;
                @include qodefTransition(opacity .2s ease-out);
            }
            
            .qodef-pl-text-outer {
                @include qodefTableLayout();
                text-align: center;
            }
            
            .qodef-pl-text-inner {
                @include qodefTableCellLayout();
                vertical-align: bottom;
                padding-bottom: 30px;
                
                .qodef-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }

            .qodef-pl-custom-atc-btn {
                display: inline-block;
                font-size: 27px;
                color: #fff;
                position: absolute;
                top: 50%;
                left: 50%;
                @include qodefTransform(translate(-50%,-50%));

                a {

                    &.add_to_cart_button {

                        &:hover {
                            background-color: #fff;

                            i {
                                color: #000;
                            }
                        }
                    }

                    @include qodefTransition(0.2s);
                    border-radius: 0;
                    padding: 30px;
                    border: none;
                    font-size: 0;
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;         
                    background-color: $additional-dark-color;
                    overflow: hidden;

                    i {
                        @include qodefTransition(0.2s);
                        color: #fff;
                        position: absolute;
                        z-index: 3;
                        margin-left: -2px;
                        font-size: 27px;
                    }

                    &.added {
                        display: none;
                    }

                    &.added_to_cart {
                        background-color: #fff;             

                        &:before {
                            @include qodefAnimation(tilt 0.5s cubic-bezier(.36,.07,.19,.97) both);
                            @include qodefTransform(translate3d(0, 0, 0));
                            color: #000;
                            position: absolute;
                            content: "\e04e";
                            font-family: "Simple-Line-Icons";
                            font-weight: 300;
                            font-size: 27px;
                            margin-left: -2px;
                            margin-top: 1px;
                            backface-visibility: hidden;
                        }
                    }

                    @keyframes tilt {
                        40% {
                          transform: translate3d(0, -5px, 0) rotate(-5deg);
                        }

                        80% {
                          transform: translate3d(0, 0, 0) rotate(0);
                        }
                    }
                }
            }
        }

        .star-rating {
            display: none;
        }

        .qodef-pl-category {
            display: inline-block;
            float: left;
            margin: 0;
            font-size: 15px;
            line-height: 15px;
            padding-bottom: 23px;
            text-transform: capitalize;
        }
        
        .qodef-pl-text-wrapper {
            @include qodefRelativeHolderLayout();
            margin: 15px 0 6px;
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.qodef-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .qodef-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */