/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();
	
	&.qodef-info-on-image {
		
		.qodef-pli {
			
			.qodef-pli-add-to-cart {
				margin: 25px 0 0;
			}

			.qodef-pli-inner {

				.qodef-pli-text {
					background: rgba(0, 0, 0, 0.4);
				}
			}

			.qodef-pli-title, .qodef-pli-category, .qodef-pli-price {
				display: block;
				float: none;
				margin-bottom: 22px;
			}

			.qodef-pli-price del {
				color: inherit;
			}
		}
	}

    .qodef-pli {
        padding: 0;
        z-index: 100;

	    .qodef-pli-title {
		    margin: 10px 0 7px;
		    display: inline-block;
		    line-height: 15px;
		    letter-spacing: .025em;
	    }
	
	    .qodef-pli-category {
		    margin: 6px 0 24px;
		    display: inline-block;
		    float: left;
		    font-size: 15px;
		    line-height: 15px;
		    font-family: Raleway, sans-serif;
	    }

        .qodef-pli-excerpt {
            color: $default-text-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .qodef-pli-rating-holder {
		    @include qodefRelativeHolderLayout();
		    width: inherit;
		    line-height: 1;
		    margin: 13px 0 3px;
		    position: relative;
		    float: right;
		    display: inline-block;
		    color: #6d6d6d;
		    vertical-align: middle;
		    overflow: hidden;
	    }

	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
	    }

        .qodef-pli-price {
	        @include qodefWooCommercePriceStyle();
        }
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .qodef-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
            @include qodefRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .qodef-pli-onsale,
            .qodef-pli-out-of-stock,
            .qodef-pli-new-product {
	            @include qodefWooCommerceMarksGeneralStyle();
            }

            .qodef-pli-onsale {
	            @include qodefWooCommerceSaleStyle();
            }

            .qodef-pli-out-of-stock {
	            @include qodefWooCommerceOutOfStockStyle();
            }
	
	        .qodef-pli-new-product {
		        @include qodefWooCommerceNewProductStyle();
	        }
        }
            
        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            box-sizing: border-box;
            @include qodefTransition(opacity .2s ease-out);
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();

            .qodef-pli-title,
            .qodef-pli-category a,
            .qodef-pli-excerpt,
            .qodef-pli-rating,
            .qodef-pli-price {
                color: #fff;
            }
	
	        .qodef-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .qodef-pli-add-to-cart {

		        &.qodef-default-skin {

			        .button,
			        .added_to_cart {
				        @include qodefButtonSolidColor();

				        &:hover {
				            @include qodefButtonSolidHoverColor();
				        }
			        }
		        }

		        &.qodef-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-text-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.qodef-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $additional-dark-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include qodefButtonDefaultStyle();
	            @include qodefButtonMediumParams();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }

            .qodef-pli-custom-atc-btn {
                display: inline-block;
                font-size: 27px;
                color: #000;

                a {

                    &.add_to_cart_button {

                        &:hover {
                            background-color: #fff;

                            i {
                                color: #000;
                            }
                        }
                    }

                    @include qodefTransition(0.2s);
                    border-radius: 0;
                    padding: 30px;
                    border: none;
                    font-size: 0;
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;         
                    background-color: $additional-dark-color;
                    overflow: hidden;

                    i {
                        @include qodefTransition(0.2s);
                        color: #fff;
                        position: absolute;
                        z-index: 3;
                        margin-left: -2px;
                        font-size: 27px;
                    }

                    &.added {
                        display: none;
                    }

                    &.added_to_cart {
                        background-color: #fff;             

                        &:before {
                            @include qodefAnimation(tilt 0.5s cubic-bezier(.36,.07,.19,.97) both);
                            @include qodefTransform(translate3d(0, 0, 0));
                            color: #000;
                            position: absolute;
                            content: "\e04e";
                            font-family: "Simple-Line-Icons";
                            font-weight: 300;
                            font-size: 27px;
                            margin-left: -2px;
                            margin-top: 1px;
                            backface-visibility: hidden;
                        }
                    }

                    @keyframes tilt {
                        40% {
                          transform: translate3d(0, -5px, 0) rotate(-5deg);
                        }

                        80% {
                          transform: translate3d(0, 0, 0) rotate(0);
                        }
                    }
                }
            }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }
    }

    .qodef-pli-text-wrapper {
        @include qodefRelativeHolderLayout();
        margin: 15px 0 0;
    }
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {

		.qodef-pli {

			.qodef-pli-inner {

				.qodef-pli-text {
					font-weight: 400;
					background: rgba(255, 255, 255, 0.7);
				}
				
				.qodef-pli-text-inner {
					
					.qodef-pli-title,
					.qodef-pli-category a,
					.qodef-pli-excerpt,
					.qodef-pli-rating,
					.qodef-pli-price {
						color: $default-text-color;
					}
					
					.qodef-pli-category a:hover {
						color: rgba($default-text-color, .8);
					}

					.qodef-pli-title {
						font-weight: 400;
					}
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */